"use client"

import { useState } from "react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Checkbox } from "@/components/ui/checkbox"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { ArrowRight, ArrowLeft, Check } from "lucide-react"

export function OnboardingSteps() {
  const [step, setStep] = useState(1)
  const totalSteps = 3

  const nextStep = () => {
    if (step < totalSteps) {
      setStep(step + 1)
    } else {
      // Final step completed, redirect to dashboard
      window.location.href = "/dashboard"
    }
  }

  const prevStep = () => {
    if (step > 1) {
      setStep(step - 1)
    }
  }

  return (
    <Card className="w-full max-w-md">
      <CardHeader className="space-y-1">
        <div className="flex items-center justify-between">
          <CardTitle className="text-2xl font-bold">
            {step === 1 && "Create an account"}
            {step === 2 && "Choose your role"}
            {step === 3 && "Select your interests"}
          </CardTitle>
          <div className="text-sm text-muted-foreground">
            Step {step} of {totalSteps}
          </div>
        </div>
        <CardDescription>
          {step === 1 && "Enter your information to create your StudentHub account"}
          {step === 2 && "Select your role to personalize your experience"}
          {step === 3 && "Choose your interests to get personalized recommendations"}
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        {step === 1 && (
          <>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="first-name">First name</Label>
                <Input id="first-name" placeholder="John" />
              </div>
              <div className="space-y-2">
                <Label htmlFor="last-name">Last name</Label>
                <Input id="last-name" placeholder="Doe" />
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input id="email" type="email" placeholder="john.doe@university.edu" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="dob">Date of Birth</Label>
              <Input id="dob" type="date" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input id="password" type="password" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="confirm-password">Confirm Password</Label>
              <Input id="confirm-password" type="password" />
            </div>
          </>
        )}

        {step === 2 && (
          <div className="space-y-4">
            <div className="space-y-2">
              <Label>I am a</Label>
              <RadioGroup defaultValue="student" className="grid gap-4 pt-2">
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="student" id="student" />
                  <Label htmlFor="student" className="flex flex-col">
                    <span className="font-medium">Student</span>
                    <span className="text-sm text-muted-foreground">
                      Access events, connect with peers, and build your portfolio
                    </span>
                  </Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="admin" id="admin" />
                  <Label htmlFor="admin" className="flex flex-col">
                    <span className="font-medium">University Admin</span>
                    <span className="text-sm text-muted-foreground">
                      Manage university events and approve student accounts
                    </span>
                  </Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="organizer" id="organizer" />
                  <Label htmlFor="organizer" className="flex flex-col">
                    <span className="font-medium">Event Organizer</span>
                    <span className="text-sm text-muted-foreground">Create and manage events for students</span>
                  </Label>
                </div>
              </RadioGroup>
            </div>
            <div className="space-y-2">
              <Label htmlFor="university">University</Label>
              <Select>
                <SelectTrigger>
                  <SelectValue placeholder="Select your university" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="nyu">New York University</SelectItem>
                  <SelectItem value="columbia">Columbia University</SelectItem>
                  <SelectItem value="harvard">Harvard University</SelectItem>
                  <SelectItem value="mit">MIT</SelectItem>
                  <SelectItem value="stanford">Stanford University</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="space-y-4">
            <div className="space-y-2">
              <Label>Select your interests (choose at least 3)</Label>
              <div className="grid grid-cols-2 gap-2 pt-2">
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="tech" />
                  <Label htmlFor="tech">Technology</Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="design" />
                  <Label htmlFor="design">Design</Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="business" />
                  <Label htmlFor="business">Business</Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="ai" />
                  <Label htmlFor="ai">Artificial Intelligence</Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="marketing" />
                  <Label htmlFor="marketing">Marketing</Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="finance" />
                  <Label htmlFor="finance">Finance</Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="healthcare" />
                  <Label htmlFor="healthcare">Healthcare</Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-3">
                  <Checkbox id="education" />
                  <Label htmlFor="education">Education</Label>
                </div>
              </div>
            </div>
            <div className="space-y-2">
              <Label>Career goals</Label>
              <Select>
                <SelectTrigger>
                  <SelectValue placeholder="Select your primary career goal" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="internship">Find an internship</SelectItem>
                  <SelectItem value="job">Land a full-time job</SelectItem>
                  <SelectItem value="networking">Build my professional network</SelectItem>
                  <SelectItem value="skills">Develop new skills</SelectItem>
                  <SelectItem value="startup">Launch a startup</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        )}
      </CardContent>
      <CardFooter className="flex flex-col space-y-4">
        <div className="flex w-full justify-between">
          {step > 1 ? (
            <Button variant="outline" onClick={prevStep}>
              <ArrowLeft className="mr-2 h-4 w-4" /> Back
            </Button>
          ) : (
            <div></div>
          )}
          <Button onClick={nextStep}>
            {step < totalSteps ? (
              <>
                Next <ArrowRight className="ml-2 h-4 w-4" />
              </>
            ) : (
              <>
                Complete <Check className="ml-2 h-4 w-4" />
              </>
            )}
          </Button>
        </div>
        {step === 1 && (
          <div className="text-center text-sm">
            Already have an account?{" "}
            <Link href="/signin" className="text-primary hover:underline">
              Sign in
            </Link>
          </div>
        )}
      </CardFooter>
    </Card>
  )
}
